import Vue from 'vue'
import VueRouter from 'vue-router'
import { routes } from './routers'
import store from '@/store'
import { getISLOGIN, setTitle, setToken, clearToken, getToken } from '@/libs/util'
import config from '@/config'
const { homeName } = config

Vue.use(VueRouter)
const router = new VueRouter({
    base: window.__POWERED_BY_QIANKUN__ ? `/${process.env.VUE_APP_CODE}` : `/web/${process.env.VUE_APP_CODE}/`,
    mode: 'history',
    scrollBehavior: () => ({ y: 0 }),
    routes
})

router.beforeEach((to, from, next) => {
    to.meta.title && setTitle(to.meta.title)

    const token =  getToken()
    const login_route = {
        name: 'login',
        params: {
            "c_url": window.location.href
        }
    }


    if (token) {
        if (!store.state.router.hasGetRules) {
            store.dispatch('authorization').then(rules => {
                store.dispatch('concatRoutes', rules).then(routers => {
                    router.addRoutes(routers)
                    next({...to, replace: true })
                }).catch((err) => {
                    setToken('')
                  next(login_route)
                })
            }).catch((err1) => {
                setToken('')
              next(login_route)
            })
        } else {
            next()
        }
    } else {
      if (to.name === 'login' || window.__POWERED_BY_QIANKUN__) {
        next() // 当去登录页，放行；或者是微前端模式，放行，由主应用控制登录
      } else {
        next(login_route) // 否则，跳转到登录页
      }
    }
})


router.afterEach(to => {
    window.scrollTo(0, 0)
})

export default router
