import axios from "axios";
import { Notice, Button } from "view-design";
import Cookies from "js-cookie";
import { Base64 } from "js-base64";
import store from "@/store";
import { gotoUnAuth } from "@/libs/qiankun";
import { replaceParamVal, setToken, clearToken, getToken } from "@/libs/util";
import config from "@/config";
const { isHttps } = config;

const copyToClipboard = (text) => {
  navigator.clipboard
    .writeText(text)
    .then(() => {
      Notice.success({
        title: "Trace ID复制成功",
      });
    })
    .catch(() => {
      Notice.error({
        title: "复制失败",
      });
    });
};

/**
 * 创建通知，包含复制 Trace ID 功能
 * @param {string} title 标题
 * @param {string} desc 描述
 * @param {string} traceId Trace ID
 * @param {string} type 通知类型 warning | error
 */
const createNotice = (title, desc, traceId, type = "warning") => {
  const noticeMethod = type === "error" ? Notice.error : Notice.warning;
  noticeMethod({
    title,
    desc,
    duration: 10,
    render: (h) => {
      return h("div", [
        h("p", title),
        desc ? h("p", desc) : null,
        h(
          Button,
          {
            props: {
              type: "info",
            },
            style: {
              marginTop: "8px",
            },
            on: {
              click: () => copyToClipboard(traceId),
            },
          },
          "复制 Trace ID发送给运维"
        ),
      ]);
    },
  });
};

class HttpRequest {
  constructor(baseUrl = baseURL) {
    this.baseUrl = baseUrl;
    this.queue = {};
  }
  getInsideConfig() {
    let config = {
      headers: {
        "X-CSRFToken": Cookies.get("csrftoken"),
        "X-Xsrftoken": Cookies.get("_xsrf"),
        appcode: "zl",
      },
    };
    if (getToken()) {
      config.headers["auth-key"] = getToken();
    }
    return config;
  }
  destroy(url) {
    delete this.queue[url];
    if (!Object.keys(this.queue).length) {
      // Spin.hide()
    }
  }
  interceptors(instance, url) {
    // 请求拦截
    instance.interceptors.request.use(
      (config) => {
        // 添加全局的loading...
        if (!Object.keys(this.queue).length) {
          // Spin.show() // 不建议开启，因为界面不友好
        }
        this.queue[url] = true;
        return config;
      },
      (error) => {
        return Promise.reject(error);
      }
    );
    // 响应拦截
    instance.interceptors.response.use(
      (res) => {
        this.destroy(url);
        const { data, status, request } = res;
        return { data, status, request };
      },
      (error) => {
        this.destroy(url);
        let traceId = "No trace ID available";
        if (error.response && error.response.headers) {
          traceId = error.response.headers["x-trace-id"] || traceId;
        }

        if (error.response.status === "401" || error.response.status === 401) {
          if (window.__POWERED_BY_QIANKUN__) {
            gotoUnAuth();
          } else {
            clearToken();
            location.reload();
          }
        } else if (error.response.status === 400) {
          createNotice(
            "数据不符合预期",
            error.response.data.msg,
            traceId,
            "warning"
          );
        } else if (error.response.status === 402) {
          createNotice("请刷新一下，再提交", null, traceId, "warning");
        } else if (error.response.status === 403) {
          createNotice(
            "你没有权限，请联系管理员",
            error.response.data,
            traceId,
            "warning"
          );
        } else if (error.response.status === 500) {
          createNotice(
            "服务内部错误，请联系管理员",
            error.response.data,
            traceId,
            "error"
          );
        }
        return Promise.reject(error);
      }
    );
  }
  request(options) {
    const instance = axios.create();
    options = Object.assign(this.getInsideConfig(), options);
    this.interceptors(instance, options.url);
    return instance(options);
  }
  get(options) {
    const instance = axios.create();
    options = Object.assign(this.getInsideConfig(), options);
    this.interceptors(instance, options.url);
    return instance(options);
  }
}
export default HttpRequest;
